import share from '@/components/share';
export default{
	components: {
		share
	},
	data() {
		return {
			specClass: 'none',
			specSelected:[],
			favorite: true,
			favorites:[],
			shareList: [],
			desc: `
				<div style="width:100%">
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i4/479184430/O1CN01nCpuLc1iaz4bcSN17_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd2.alicdn.com/imgextra/i2/479184430/O1CN01gwbN931iaz4TzqzmG_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd3.alicdn.com/imgextra/i3/479184430/O1CN018wVjQh1iaz4aupv1A_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd4.alicdn.com/imgextra/i4/479184430/O1CN01tWg4Us1iaz4auqelt_!!479184430.jpg_400x400.jpg" />
					<img style="width:100%;display:block;" src="https://gd1.alicdn.com/imgextra/i1/479184430/O1CN01Tnm1rU1iaz4aVKcwP_!!479184430.jpg_400x400.jpg" />
				</div>
			`,
			specList: [
				{
					id: 1,
					name: '尺寸',
				},
				{	
					id: 2,
					name: '颜色',
				},
			],
			specChildList: [
				{
					id: 1,
					pid: 1,
					name: 'XS',
				},
				{
					id: 2,
					pid: 1,
					name: 'S',
				},
				{
					id: 3,
					pid: 1,
					name: 'M',
				},
				{
					id: 4,
					pid: 1,
					name: 'L',
				},
				{
					id: 5,
					pid: 1,
					name: 'XL',
				},
				{
					id: 6,
					pid: 1,
					name: 'XXL',
				},
				{
					id: 7,
					pid: 2,
					name: '白色',
				},
				{
					id: 8,
					pid: 2,
					name: '珊瑚粉',
				},
				{
					id: 9,
					pid: 2,
					name: '草木绿',
				},
			],
			content:{},
			banner:[],
			baseURL:this.$http.jingtai,
		};
	},
	async onLoad(options){
		
		//接收传值,id里面放的是标题，因为测试数据并没写id 
		let id = options.id;
		this.content = await this.$http.getOneGoods(id);
		this.banner = this.content.banner.split(";");
		this.content.content = this.content.content.replace("src=\"","src=\""+this.baseURL);
		
		if(uni.getStorageSync("history")){
			var history = uni.getStorageSync("history");
		}else{
			var history = []
		}
		if(!history.includes(id)){
			let res = await this.$http.addViews(id,this.content.views+1);
			if(res === 200){
				this.content.views+=1;
				history.push(id);
				uni.setStorageSync("history",history);
			}
		}
		
		if(uni.getStorageSync("favorites")){
			this.favorites = uni.getStorageSync("favorites")
		}else{
			this.favorites = [];
		}
		if(this.favorites.includes(this.content.id)){
			this.favorite = true;
		}else{
			this.favorite = false;
		}
	},
	methods:{
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		//选择规格
		selectSpec(index, pid){
			let list = this.specChildList;
			list.forEach(item=>{
				if(item.pid === pid){
					this.$set(item, 'selected', false);
				}
			})

			this.$set(list[index], 'selected', true);
			//存储已选择
			/**
			 * 修复选择规格存储错误
			 * 将这几行代码替换即可
			 * 选择的规格存放在specSelected中
			 */
			this.specSelected = []; 
			list.forEach(item=>{ 
				if(item.selected === true){ 
					this.specSelected.push(item); 
				} 
			})
			
		},
		//分享
		share(){
			this.$refs.share.toggleMask();	
		},
		//收藏
		toFavorite(){
			this.favorite = !this.favorite;
			if(this.favorite){
				this.favorites.push(this.content.id)
			}else{
				this.favorites = this.favorites.filter(v => v !== this.content.id);
			}
			uni.setStorageSync("favorites",this.favorites)
		},
		buy(){
			let shoppingCar = this.$store.state.shoppingCar;
			let goods = shoppingCar.find(v=>v.id === this.content.id)
			if(goods){
				goods.count+=1;
				this.$store.commit("change",goods)
			}else{
				this.content.count = 1;
				this.$store.commit("change",this.content)
			}
			uni.showToast({
				title:"加入成功",
			})
		},
		stopPrevent(){}
	},

}
